.card {
  max-width: 600px;
  background-color: hsl(0deg, 0%, 100%);
  border-radius: 12px;
}
@media (min-width: 768px) {
  .card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0;
  }
}

.card__img {
  background-image: url(../img/image-product-mobile.webp);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 20rem;
  border-radius: 12px 12px 0 0;
}
@media (min-width: 768px) {
  .card__img {
    background-image: url(../img/image-product-desktop.webp);
    height: 100%;
    border-radius: 12px 0 0 12px;
  }
}

.card__info {
  display: flex;
  flex-direction: column;
  gap: 0.7rem;
  padding: 2rem;
}
.card__info .card__subtitle {
  text-transform: uppercase;
  letter-spacing: 0.4rem;
}
.card__info .card__title {
  font-size: 30px;
  color: hsl(212deg, 21%, 14%);
}
.card__info p {
  color: hsl(228deg, 12%, 48%);
}

.card__price {
  margin: 1rem 0;
  display: flex;
  gap: 1.5rem;
  align-items: center;
}
.card__price .card__price--retail {
  font-family: "Fraunces", serif;
  font-size: 30px;
  color: hsl(158deg, 36%, 37%);
}
.card__price .card__price--discount {
  text-decoration: line-through;
}

.container {
  padding: 0 1rem;
}

.btn {
  padding: 0.8rem;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  border-style: none;
  border-radius: 8px;
  transition: all 0.5s ease;
  cursor: pointer;
}

.btn--shop {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  background-color: hsl(158deg, 36%, 37%);
  font-weight: 700;
  color: white;
}

.btn:hover {
  background-color: #2c5e4b;
}

.card-section {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .card-section {
    height: 90vh;
  }
}
@media (min-width: 480px) {
  .card-section {
    margin-top: 2rem;
  }
}